import styled from 'styled-components';
import {MdKeyboardArrowRight, MdArrowForward} from 'react-icons/md'

import '../../index.css';
import marketpic from '../../images/tozai/tozai-market-front.jpg';


export const HeroContainer = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30 px;
    height: 800px;
    position: relative;
    z-index: 1;

`;

export const HeroBg = styled.div`

    position: absolute;
    display: flex;
    justify-content:center;

    background-image: linear-gradient(rgba(0,0,0,0.1), 
    rgba(0,0,0,0.7)), url(${marketpic});
    background-position: center;
    background-size: cover;

    width: 100%;
    height: 100%;
    overflow: hidden;
`;

export const HeroContent = styled.div`
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const HeroH1 = styled.h1`
    color: #fff;
    font-size: 48px;
    text-align: center;
    text-shadow:
    -1px -1px 0 #000,  
    1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;

    @media screen and (max-width: 768px){
        font-size:40px;
    }

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const HeroP = styled.p`
    margin-top: 24px;
    color: #fff;
    font-size: 14px;
    text-align: center;
    max-width: 600px;

    @media screen and (max-width: 768px){
        font-size:20px;
    }

    @media screen and (max-width: 480px){
        font-size: 16px;
    }
`

export const HeroBtnWrapper = styled.div`
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const ArrowForward = styled(MdArrowForward)`
    margin-left: 8px;
    font-size: 20px;
`

export const ArrowRight= styled(MdKeyboardArrowRight)`
    margin-left: 8px;
    font-size: 20px;
`

export const PFPWrapper = styled.img`
    height: 200px;
    width: 200px;
    border-radius: 50%;
    border: 4px solid white;
`