import React, { useState } from 'react';
import { CarouselData } from './CarouselData';
import { FaArrowAltCircleRight,FaArrowAltCircleLeft, FaSlideshare } from 'react-icons/fa';
import { CarouselContainer, CarouselImg, LeftArrow, RightArrow } from './CarouselSection';

const CarouselSection = ({data}) => {
const [current, setCurrent] = useState(0)
const length = data.length

    const nextSlide = () => {
        setCurrent(current === length - 1 ? 0 : current + 1)
    }
    const prevSlide = () => {
        setCurrent(current === 0 ? length -1 : current - 1)
    }

    if(!Array.isArray(data) || FaSlideshare.length <= 0) {
        return null;
    }

    return (
        <CarouselContainer>
            {/* <h1>View our food selection</h1> */}
            <LeftArrow>
                <FaArrowAltCircleLeft onClick={prevSlide} />
            </LeftArrow>
            <RightArrow>
                <FaArrowAltCircleRight onClick={nextSlide} />
            </RightArrow>
            
            {CarouselData.map((slide, index) => {

                return (
                    <div className={index === current ? 'slide active' : 'slide'} key={index}>
                        {index === current && (
                            <CarouselImg src={slide.image} alt={slide.desc}/>
                            
                        )}
                    </div>
                )

            })}
        </CarouselContainer>
    )
};

export default CarouselSection;

