import React, {useState} from 'react'

import {HeroContainer,
    HeroBg, 
    HeroContent, 
    HeroH1, 
    HeroP, 
    HeroBtnWrapper,
    PFPWrapper, 
    ArrowForward, 
    ArrowRight } from './HeroElements';
import {Button} from '../ButtonElements';

import marketpic from '../../images/tozai/tozai-market-front.jpg';



// import { FaArrowRight } from 'react-icons/fa';


export const HeroSection = () => {
const [hover, setHover] = useState(false);
    const onHover = () => {
        setHover(!hover);
    }

    return (
        <HeroContainer id="home">
            <HeroBg>
                
            </HeroBg>
            <HeroContent>
                <HeroH1>
                    Welcome to Tozai Foods Market
                </HeroH1>
                <HeroP> Disclaimer: not the official website by Tozai Market. This website was made as a project by Cole Kim for the Snap Engineering Academy scholarship application.
                </HeroP>
            <HeroBtnWrapper>
                <Button to="about" 
                    onMouseEnter ={onHover} 
                    onMouseLeave ={onHover}
                    primary = 'false'
                    dark='false'
                    smooth={true}
                    duration={500}
                    spy={true}
                    exact='true'
                    offset={-80}
                >
                    Learn More {hover ? <ArrowForward/>:<ArrowRight/>}
                </Button>
            </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    );
};
